// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'
// ** Table Columns
// ** Store & Actions
import { getAllpredictor} from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
import BreadCrumbs from '@components/breadcrumbs'
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Third Party Components

import ReactPaginate from 'react-paginate'
import { ChevronDown, MoreVertical, Edit } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, Label, Row, Col, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'

const DataTableTransaction = () => {

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.predictor)
  
  // ** States

  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])
 

  useEffect(() => {
   
    dispatch(getAllpredictor(currentPage))
  }, [currentPage])
  console.log(currentPage)

  // ** Get data on mount
  // ** Function to handle filter
  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.predictor_name.filter(item => {
        const startsWith =
          item.title?.toString().toLowerCase().startsWith(value.toLowerCase()) 
          // item.caption?.toString().toLowerCase().startsWith(value.toLowerCase()) ||
          // item.type?.toLowerCase().startsWith(value.toLowerCase())

        const includes =
          item.title?.toString().toLowerCase().includes(value.toLowerCase())
          // item.caption?.toString().toLowerCase().includes(value.toLowerCase()) ||
          // item.type?.toLowerCase().includes(value.toLowerCase())

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    }
  }
console.log(handleFilter)

  const columns = [
    // {
    //   name: '#',
    //   minWidth: '20px',
    //   sortable: true,
    //   cell: (row, pos) => `#${store.adData?.data?.from + pos}`
    // },
    
    {
      name: 'Name',
      minWidth: '100px',
      selector: 'title',
      sortable: true,
      cell: (row) => row?.title
    },
    {
      name: 'Caption',
      minWidth: '100px',
      selector: 'caption',
      sortable: true,
      cell: (row) => row?.caption
    },
    {
      name: 'Participants',
      minWidth: '100px',
      selector: 'joined_count',
      sortable: true,
      cell: (row) => row?.joined_count
    },
   
   
    {
      name: 'Entry Fee',
      minWidth: '100px',
      selector: 'entry_fee',
      sortable: true,
      cell: (row) => row?.entry_fee
    },
    {
      name: 'Total question',
      minWidth: '100px',
      selector: 'no_of_questions',
      sortable: true,
      cell: (row) => row?.no_of_questions
    },
    {
        name: 'Description', 
        minWidth: '100px',
        selector: 'description',
        sortable: true,
        cell: (row) => row?.description
      },
      {
        name: 'Active',
        minWidth: '138px',
        selector: 'active',
        sortable: false,
        cell: () => (
          <UncontrolledDropdown>
          <DropdownToggle className='pr-1' tag='span'>
            <MoreVertical size={15} />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem tag='a'  className='w-100'  >  
            {/* onClick={() => editHandler(row)} */}
              <Edit size={15}/>
              <span className='align-middle ml-50'>Edit</span>
            </DropdownItem>
           
            {/* <DropdownItem tag='a' className='w-100' onClick={() => handleConfirmText(row.feed_id)}>
              <Trash2 size={15} />
              <span className='align-middle ml-50'>Delete</span>
            </DropdownItem> */}
          </DropdownMenu>
        </UncontrolledDropdown>
        )
      }
    
  ]
  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function to handle per page

  const handlePerPage = e => {
    setRowsPerPage(parseInt(e.target.value))
  }

  // ** Custom Pagination
  const CustomPagination = () => {

    const count = store.predictor_name?.length ? store.predictor_name?.last_page : 1
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }
  console.log(store.adData)
  // ** Table data to render
  const dataToRender = () => {
    if (store.predictor_name?.length > 0) {
      return searchValue.length ? filteredData : store.predictor_name?.slice(0, rowsPerPage)
    } else {
      return []
    }
  }
console.log(store.isloading)
  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }
  
  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Cricket Predictor' breadCrumbActive='List' />
      <Card>
        <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>Show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={rowsPerPage}
                onChange={e => handlePerPage(e)}
              >
                <option value={10}>10</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>Entries</Label>
            </div>
          </Col>
          {/* <Col className='d-flex align-items-center justify-content-sm-end mt-sm-0 mt-1' sm='6'>
            <Label className='mr-1' for='search-input'>
              Search
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
              placeholder='search...'
            />
          </Col> */}
        </Row>
        <DataTable
          noHeader
          pagination
          paginationServer
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
    </Fragment>
  )
}

export default memo(DataTableTransaction)